// Global Style Component

import styled, {createGlobalStyle} from 'styled-components'

export const GlobalStyle = createGlobalStyle`
body {
    font-family: 'Work Sans', sans-serif;
    margin: 0;
    padding: 0;
    /* helps with font smoothing */
    -webkit-font-smoothing: antialiased;
    /* has browser focus on legibility over speed & geometic precision   */
    text-rendering: optimizeLegibility;
}
`

export const Pill = styled.div`
  background: #41bf8a;
  border: 2px solid #000;
  border-radius: 30px;
  height: 20px;
  width: 20px;
  padding: 8px;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  display: flex;
`

export const Close = styled.button`
  background: none;
  border: 0;
  cursor: pointer;
  height: 24px;
  width: 24px;
  padding: 0;
  position: relative;

  /* creating 'x' icon for pannel  */
  &::before,
  &::after {
    background-color: #000;
    content: '';
    height: 24px;
    width: 2px;
    position: absolute;
    top: 0;
    left: 9px;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
`
